import 'antd/dist/antd.css';
import { Form, Input, Row, Col, Table } from 'antd';
import { useEffect, useState } from 'react';
import { printa } from './printa_data';

function App() {

  const [fields, setFields] = useState({
    pantone: '',
    cmyk_c: '',
    cmyk_m: '',
    cmyk_y: '',
    cmyk_k: '',
    rgb_r: '',
    rgb_g: '',
    rgb_b: '',
    hex: '',
  });

  const [data, setData] = useState(printa);
  const printa_length = printa.length;
  const columns = [
    {
      title: 'Ref Pantone',
      dataIndex: 'pantone',
      key: 'pantone',
      render : (text, values) => {
        return <div className='cubo__container'>
          <div className='cubo' style={{background : '#' + values.hex.toString() }}></div>
          <span>{text}</span>
        </div>
      }
     
    },
    {
      title: 'Valores CMYK',
      dataIndex: 'cmyk_c',
      key: 'cmyk_c',
      render: (_, values) => {
        console.log(values);
        return values.cmyk_c.toString() +"/"+ values.cmyk_m.toString()+"/"+ values.cmyk_y.toString() +"/"+ values.cmyk_k.toString()  ;
      },
    },
    {
      title: 'Valores RGB',
      dataIndex: 'rgb_r',
      key: 'rgb_r',
      render: (_, values) => {
        console.log(values);
        return values.rgb_r.toString() +"/"+ values.rgb_g.toString()+"/"+ values.rgb_b.toString();
      },
    },
    {
      title: 'Hexadecimal',
      dataIndex: 'hex',
      key: 'hex',
      render : text  => '#' + text,
    } 
  ]
  useEffect(() => {
    console.log('Do something after counter has changed', fields);
    setData(printa.filter((k) => {
      
      return k.pantone.toLowerCase().includes(fields.pantone.toLowerCase()) 
            && k.cmyk_c.toString().toLowerCase().includes(fields.cmyk_c.toLowerCase()) 
            && k.cmyk_m.toString().toLowerCase().includes(fields.cmyk_m.toLowerCase()) 
            && k.cmyk_y.toString().toLowerCase().includes(fields.cmyk_y.toLowerCase()) 
            && k.cmyk_k.toString().toLowerCase().includes(fields.cmyk_k.toLowerCase()) 
            && k.rgb_r.toString().toLowerCase().includes(fields.rgb_r.toLowerCase()) 
            && k.rgb_g.toString().toLowerCase().includes(fields.rgb_g.toLowerCase()) 
            && k.rgb_b.toString().toLowerCase().includes(fields.rgb_b.toLowerCase()) 
            && k.hex.toString().toLowerCase().includes(fields.hex.toLowerCase());
    }  ));
  }, [fields]);

  const handlePantone = (event) => {

    setFields((k) => ({ ...k, pantone: event.target.value }));
    // setData(printa.filter((k) => k.pantone.includes(event.target.value)));
  }
  const handlecmyk_c = (event) => {
    setFields((k) => ({ ...k, cmyk_c: event.target.value }));
  }
  const handlecmyk_m = (event) => {
    setFields((k) => ({ ...k, cmyk_m: event.target.value }));
  }
  const handlecmyk_y = (event) => {
    setFields((k) => ({ ...k, cmyk_y: event.target.value }));
  }
  const handlecmyk_k = (event) => {
    setFields((k) => ({ ...k, cmyk_k: event.target.value }));
  }
  const handlergb_r = (event) => {
    setFields((k) => ({ ...k, rgb_r: event.target.value }));
  }
  const handlergb_g = (event) => {
    setFields((k) => ({ ...k, rgb_g: event.target.value }));
  }
  const handlergb_b = (event) => {
    setFields((k) => ({ ...k, rgb_b: event.target.value }));
  }
  const handlehex = (event) => {
    setFields((k) => ({ ...k, hex: event.target.value }));
  }

  return (
    <section className='master__max'>

      <div className='formulario_search'>

        <Form
          name="basic"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 19 }}
          initialValues={{ remember: true }}
          autoComplete="off"
        
        >
          <Form.Item label="Ref. Pantone" >
            <Input
              autoFocus={true}
              name='pantone'
              value={fields.pantone}
              onChange={handlePantone} />
          </Form.Item>

          <Form.Item label="Valores CMYK" >
            <Row>
              <Col span={6}>
                <Input
                  name="cmyk_c"
                  onChange={handlecmyk_c}
                  placeholder='C'
                  value={fields.cmyk_c}
                />
              </Col>
              <Col span={6}>
                <Input
                  name="cmyk_m"
                  placeholder='M'
                  onChange={handlecmyk_m}
                  value={fields.cmyk_m} />
              </Col>
              <Col span={6}>
                <Input
                  name="cmyk_y"
                  placeholder='Y'
                  onChange={handlecmyk_y}
                  value={fields.cmyk_y} />
              </Col>
              <Col span={6}>
                <Input
                  name="cmyk_k"
                  placeholder='K'
                  onChange={handlecmyk_k}
                  value={fields.cmyk_k} />
              </Col>
            </Row>

          </Form.Item>

          <Form.Item label="Valores RGB">
            <Row>
              <Col span={8}>
                <Input
                  name="rgb_r"
                  placeholder='R'
                  onChange={handlergb_r}
                  value={fields.rgb_r} />
              </Col>
              <Col span={8}>
                <Input
                  name="rgb_g"
                  placeholder='G'
                  onChange={handlergb_g}
                  value={fields.rgb_g} />
              </Col>
              <Col span={8}>
                <Input
                  name="rgb_b"
                  placeholder='B'
                  onChange={handlergb_b}
                  value={fields.rgb_b} />
              </Col>
            </Row>
          </Form.Item>


          <Form.Item label="Código HEX">
            <Input
              name="hex"
              value={fields.hex}
              onChange={handlehex}
            />
          </Form.Item>




        </Form>
      </div >
      {data.length !== printa_length && <Table columns={columns} dataSource={data} />}

    </section>
  );
}

export default App;
